import React from 'react';

import { 
    Container, 
    Main,
} from '../../styles/Layout';

import {
    WrapperLogin,
    WrapperLoginIcon,
    WrapperLoginButton,
    WrapperCadastro,
} from './styles';

import { GOOGLECLIENTID } from '../../environment';
import { Login, ErroLogin } from '../../utils/Auth';

import GoogleLogin from 'react-google-login';
import { useHistory, useLocation } from "react-router-dom";

const TelaLogin = () => {


    let history = useHistory();
    let location = useLocation();
    let { from } = location.state || { from: { pathname: "/" } };
    
    const __Login = (googleResp) => {
        Login(googleResp).then( resp => {
            if(resp.result){
                history.replace(from);
            }else{
                console.log('Erro no login')
            }
        })
    }

    return (
        <Container>
                <Main>
                    <WrapperLogin>             
                        <GoogleLogin
                            clientId={GOOGLECLIENTID}
                            buttonText="Login"
                            onSuccess={__Login}
                            onFailure={ErroLogin}
                            cookiePolicy={'single_host_origin'}
                        />
                    </WrapperLogin>
                </Main>
        </Container>
    );
}

export { TelaLogin };
